.toggle-button{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height:24px;
    width: 30px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    box-sizing: border-box;
}
.toggle-button:focus {
    outline: none;
}
.toggle-button__line{
    width: 26px;
    height: 2px;
    background: rgb(56, 56, 56);
}
@media (min-width: 769px){
    .toggle-button {
        display: none;
    }
}